<template>
  <div class="title-wrapper">
    <side-menu/>
    <div class="title">{{modules}}</div>
  </div>
  <router-link to="/quiz" 
      class="level-block" 
      v-for='i in levels.length' :key='i'
      @click='setCurrentLevelIndex(i)'>
    <span class="material-icons">widgets</span>
    <span class="level-block-body">
      <div class="level-name">{{i}} &thinsp; {{levels[i-1].name}}</div>
      <div class="level-score" :style='levels[i-1].score ? "color:gray" : "color:lightgray"'>
        {{levels[i-1].score ? `${levels[i-1].score} / ${levels[i-1].maxscore} балів` : 'Не розпочато'}}
      </div>
    </span>
    <div class="progress-bar-wrapper" 
      :style ='levels[i-1].score ? `animation: col 1s -${levels[i-1].score / levels[i-1].maxscore}s linear forwards paused` : `background: lightgray`'>
      <progress-bar :ratio='levels[i-1].score / levels[i-1].maxscore'/>
    </div>
  </router-link>
</template>

<script>
import sideMenu from '@/components/side-menu.vue'
export default {
  components: { sideMenu },
  computed: {
    levels() { return this.$store.state.levels },
    modules() { return this.$store.state.modules[localStorage.currentModuleIndex] }
  },
  methods: {
    setCurrentLevelIndex(index) { localStorage.setItem("currentLevelIndex", index) }
  }
}
</script>

<style lang="scss" scoped>

.title-wrapper { 
  display: flex;
  align-items: center;
  margin-bottom: 20px; 
}
.level-block {
  width: 100%;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 5px;
  background: white;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,.1);
}
.material-icons {
  font-size: 3em !important;
  font-weight: 100 !important;
  color: #323232;
  margin: 0 .4em;
}
.level-block-body {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-grow: 1;
  height: 6em;
  @media (min-width: 500px) { box-shadow: 8px 0 8px -6px rgba(0,0,0,1); }
}
.level-name {
  font-weight: bold; 
  font-size: 1.1em;
  margin: -10px 0;
}
.level-score {
  margin: -10px 0;
  color: lightgray;
}
.progress-bar-wrapper {
  height: 6em;
  width: 7em;
  border-radius: 0 8px 8px 0;
  @media (max-width: 499px) { display: none }
  div {
    width: 6em;
    height: 5em;
    margin: .5em;
  }
}
</style>